import React from 'react'
import "./logIn.css"
import {graphql, useStaticQuery} from 'gatsby'
import FirstPage from './index'
import {Router, Link, navigate} from '@reach/router'
import {render} from 'react-dom'
function Query(){
    const data = useStaticQuery(graphql`
    query MyQuery {
            site{
              siteMetadata{
                postInfo{
                  username
                  password
                }
              }
            }
          } 
          `)
    console.log(data.site.siteMetadata.postInfo);
    
    
    return(
        
                <LogIn path = "/logIn" data = {data.site.siteMetadata.postInfo}/>
           
        
        
    )
}

function Button(username, password, data){
    console.log(username, password, data)

    return <button class = "button" onClick = {()=>navigate('/')}>Click me</button>


}

class LogIn extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            first : "",
            last : "",
            data: this.props.data,
            
        };
    
    }
    
    updateInput(e){
        this.setState({first: e.target.value})
    }
    updateLast(e){
        this.setState({last: e.target.value})
    }
    
    render(){
        console.log("frane"+this.state.data)
        return(
            <div class = "content">
             <div class = "firstName">
                 <label>First Name: </label>
                 <input class = "first" value = {this.state.first} type = "text" placeholder = "First Name ..." onChange = {this.updateInput.bind(this)}/>
             </div>
             <div class = "firstName">
                 <label>Last Name: </label>
                 <input class = "last" value = {this.state.last} type = "text" placeholder = "Last Name ..." onChange = {this.updateLast.bind(this)}/>
             </div>
             
            <Button username = {this.state.first} password = {this.state.last} data = {this.state.data}/>
         </div>
        );
    }
}

export default LogIn